import React from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';
import store from './redux/store';
import UsersContainer from './components/users/UsersContainer';
import NavigationBar from './components/layouts/NavigationBar';
import Dashboard from './components/layouts/Dashboard';
import HomeComponent from './components/layouts/HomeComponent';
import LoginComponent from './components/authentications/LoginComponent';
import RegisterComponent from './components/authentications/RegisterComponent';
import CreateUserComponent from './components/users/CreateUserComponent';
import ScoreBoardComponent from './components/scoreBoards/ScoreBoardComponent';

function App() {
  return (
    <Provider store={store}>
      <Router>
        <div className="app">
          <NavigationBar />
          <Route exact path="/" component={HomeComponent}></Route>
          <Route path="/dashboard" component={Dashboard}></Route>
          <Route path="/login" component={LoginComponent} />
          <Route path="/register" component={RegisterComponent} />
          <Route path="/users/create-user" component={CreateUserComponent} />
          <Route path="/score-board" component={ScoreBoardComponent} />
          <Route exact path="/users" component={UsersContainer}></Route>
          {/* <Route path={`/users/`} render="" component={UsersContainer}></Route> */}
        </div>
      </Router>
    </Provider>
  );
}

export default App;
