import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export class HomeComponent extends Component {
  render() {
    return (
      <div>
        <h3>WELCOME</h3>
        <p>Please Sign In</p>
        <Link className="btn btn-primary" to="/login">
          Login
        </Link>
      </div>
    );
  }
}

export default HomeComponent;
