import React, { Component } from 'react';
import { connect } from 'react-redux';

import './users.css';
import { createUser } from '../../redux/users/actions/createUserActions';

export class CreateUserComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      username: '',
      email: '',
      password: '',
      passwordConfirmation: '',
      phoneNumber: '',
      department: '',
      sex: '',
      address: '',
      roles: [],
      errors: {}
    };
  }

  onChange = e => this.setState({ [e.target.name]: e.target.value });
  onSubmit = e => {
    this.setState({
      errors: {}
    });
    e.preventDefault();
    this.props.createUser(this.state);
  };

  onRoleChange = e => {
    let roles = [...this.state.roles];
    roles.push(e.target.value);

    this.setState({
      roles
    });
  };

  render() {
    const {
      username,
      password,
      email,
      passwordConfirmation,
      phoneNumber,
      department
    } = this.state;
    return (
      <React.Fragment>
        <div className="card bg-light">
          <article className="card-body mx-auto" style={{ maxWidth: '400px' }}>
            <h4 className="card-title mt-3 text-center">
              Create an Employee's Account
            </h4>

            <form onSubmit={this.onSubmit}>
              {/* Username */}
              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-user"></i>
                  </span>
                </div>
                <input
                  onChange={this.onChange}
                  name="username"
                  className="form-control"
                  placeholder="Full name"
                  type="text"
                  value={username}
                />
              </div>

              {/* Email */}
              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-envelope"></i>
                  </span>
                </div>
                <input
                  onChange={this.onChange}
                  name="email"
                  className="form-control"
                  placeholder="Email address"
                  type="email"
                  value={email}
                />
              </div>

              {/* Phone Number */}
              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-phone"></i>
                  </span>
                </div>
                <input
                  onChange={this.onChange}
                  name="phoneNumber"
                  className="form-control"
                  placeholder="Phone number"
                  type="text"
                  value={phoneNumber}
                />
              </div>
              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-building"></i>
                  </span>
                </div>
                <select
                  onChange={this.onChange}
                  name="department"
                  value={department}
                  className="form-control"
                >
                  <option value=""> Select Department</option>
                  <option value="finance">Finance</option>
                  <option value="IT">IT</option>
                  <option value="HR">Accoutant</option>
                </select>
              </div>

              {/* Role */}
              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-building"></i>
                  </span>
                </div>
                <select
                  onChange={this.onRoleChange}
                  name="role"
                  className="form-control"
                >
                  <option> Select Job Type</option>
                  <option value="driver">Driver</option>
                  <option value="administrator">Manager</option>
                  <option value="lineManager">Accoutant</option>
                </select>
              </div>

              {/* Genger */}

              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-user"></i>
                  </span>
                </div>
                <div onChange={this.onChange}>
                  <label className="radio-inline mx-5">
                    <input type="radio" name="sex" value="Male" />
                    Male
                  </label>
                  <label className="radio-inline">
                    <input type="radio" name="sex" value="Female" />
                    Female
                  </label>
                </div>
              </div>

              {/* Password */}
              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-lock"></i>
                  </span>
                </div>
                <input
                  className="form-control"
                  placeholder="Create password"
                  name="password"
                  type="password"
                  value={password}
                  onChange={this.onChange}
                />
              </div>

              {/* Password confirmation */}
              <div className="form-group input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-lock"></i>
                  </span>
                </div>
                <input
                  className="form-control"
                  placeholder="Repeat password"
                  type="password"
                  name="passwordConfirmation"
                  value={passwordConfirmation}
                  onChange={this.onChange}
                />
              </div>

              <div className="form-group">
                <button type="submit" className="btn btn-primary btn-block">
                  Create Account
                </button>
              </div>
            </form>
          </article>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    createdUserData: state.createUserReducer
  };
};

// const mapDispatchToProps = dispatch => {
//   return {
//     createUser: () => dispatch(createUser)
//   };
// };

export default connect(mapStateToProps, { createUser })(CreateUserComponent);
