import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

export class NavigationBar extends Component {
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <NavLink activeClassName="active" className="navbar-brand" to="/">
          3D Services
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <NavLink
                activeClassName="active"
                className="nav-link"
                to="/administration"
              >
                Administration
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                activeClassName="active"
                className="nav-link"
                to="/score-board"
              >
                Score Board
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                activeClassName="active"
                className="nav-link"
                to="/dashboard"
              >
                Dashboard
              </NavLink>
            </li>
          </ul>

          {/*  Only accessible for users who are not logged in */}
          <ul className="navbar-nav">
            <li className="nav-item">
              {/* This className active is only applied when the route is clicked
                  The routerLink navigates the user to the current route + variable */}
              <NavLink
                activeClassName="active"
                className="nav-link"
                to="/login"
              >
                Login
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                activeClassName="active"
                className="nav-link"
                to="/register"
              >
                Register
              </NavLink>
            </li>
          </ul>

          <ul className="navbar-nav">
            <li className="nav-item">
              <NavLink className="nav-link" activeClassName="active" to="/cart">
                <i className="fa fa-camera"></i>
              </NavLink>
            </li>
            {/* For loggedin user */}
            <li className="nav-item">
              <NavLink
                activeClassName="active"
                className="nav-link"
                to="/logout"
                style={{ cursor: 'pointer' }}
              >
                Logout julius@gmail.com
              </NavLink>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}

export default NavigationBar;
